.images-exposition-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-left: 20vw;
  margin-right: 20vw;
  margin-top: 5vh;
}

.image-exposition {
  width: 80vw;
  padding-bottom: 10px;
}

.paint__information-container {
  margin-top: -10px;
  margin-bottom: 16px;
}

.paint__number,.paint__title,.paint__dimensions,.paint__date,.paint__price {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #596A62;
  font-size: 14px;
  font-weight: 500;
  text-decoration: none;
}

.paint__dimensions {
  padding-bottom: 20px;
}

.title-exposition {
  padding: 0px 0px 30px 20px;
  margin-top: 54px;
  font-family: 'Josefin Sans', sans-serif;
  font-weight: 500;
  font-size: 22px;
  color: #4A4A4A;
}

@media (min-width: 320px) and (max-width: 480px) {
  .paint__number,.paint__title,.paint__dimensions,.paint__date,.paint__price {
    font-size: 10px;
  }

  .title-exposition {
    font-size: 17px;
  }
}

@media (min-width: 481px) {
  .paint__number,.paint__title,.paint__dimensions,.paint__date,.paint__price {
    font-size: 22px;
  }

  .title-exposition {
    font-size: 46px;
  }
}

@media (min-width: 1023px) {
  .image-exposition {
    padding-top: 50px;
    padding-bottom: 30px;
  }

  .paint__number,.paint__title,.paint__dimensions,.paint__date,.paint__price {
    font-size: 50px;
  }

  .title-exposition {
    padding: 30px 0px 40px 50px;
    font-size: 90px;
  }
}
